.main-page {
  color: white;
  padding: 6px;
  background: transparent;
  font-family: sans-serif;
}

.app-title {
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 20px;
  margin-top: -0.2em;
}

.balance-section {
  margin-left: -1em;
  background: transparent;
  border-radius: 10px;
  padding: 10px 0;
  margin-bottom: 20px;
}

.balance-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 12px;
}

.balance-label {
  font-weight: bold;
  font-size: 25px;
}

.balance-amount {
  font-size: 32px;
  font-weight: bold;
  margin: 4px 0;
  margin-top: 0.3em;
}

.wallet-link {
  font-size: 13px;
  color: #999;
  margin-top: 0.5em;
}

.logo-circle {
  width: 48px;
  height: 48px;
  background: white;
  color: black;
  font-weight: bold;
  font-size: 20px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.get-flyp-button {
  margin: 16px auto 4px;
  margin-top: 1.6em;
  margin-left: 0.5em;
  display: block;
  background: linear-gradient(145deg, #ffffff, #f0f8ff);
  color: black;
  font-weight: bold;
  border: none;
  padding: 12px 50px;
  width: 350px;
  border-radius: 15px;
  font-size: 22px;
}



.get-now {
  text-align: center;
  color: #999;
  font-size: 15px;
  margin-bottom: 16px;
  margin-top: 0.65em;
  margin-left: 0.9em;
}

.upgrade-button.disabled {
  padding: 8px 20px;
  border-radius: 12px;
  background: white;
  color: black;
  font-weight: 500;
  border: none;
  font-size: 20px;
  margin-left: -10em;
  margin-top: 5em;
}


.level-card {
  height: 160px;
  background: rgba(17, 17, 17, 0.2);
  border: 2px solid #333;
  border-radius: 15px;
  padding: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.level-title {
  font-weight: bold;
  font-size: 22px;
  margin-left: 0.5em;
  margin-top: -3em;
  width: 150px;
}

.level-subtitle {
  width: 200px;
  margin-left: 0.5em;
  margin-top: 0.2em;
  font-size: 18px;
  color: white;
}


.upgrade-button {
  padding: 8px 20px;
  border-radius: 12px;
  background: white;
  color: black;
  font-weight: 500;
  border: none;
  font-size: 20px;
  margin-left: -10em;
  margin-top: 5em;
}

.upgrade-button-a {
  font-size: 20px;
  text-decoration: none;
  color: black;
}

.features-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  margin-top: 20px;
}

.feature-box {
  height: 65px;
  width: 30%;
  background: rgba(17, 17, 17, 0.2);
  border: 2px solid #333;
  border-radius: 15px;
  padding: 12px 0;
  text-align: center;
  font-size: 14px;
}

.red-dot {
  width: 16px;
  height: 16px;
  background: red;
  border-radius: 50%;
  margin: 0 auto 6px;
}

.logo-image {
  width: 48px;
  height: 48px;
  border-radius: 50%;
}

.get-img {
  width: 25px;
  height: 25px;
  position: relative;
  top: 3.5px; /* move it down */
}


.dot-img {
  width: 50px;
  height: 50px;
  display: block;
  margin: 0 auto 6px;
}

.level-img {
  width: 140px;
  height: 140px;
  margin-bottom: -2em;
  margin-top: -1.7em;
  margin-left: -2.5em;
}

.dot-small-img {
  width: 10px;
  height: 10px;
  margin-left: 4px;
}

.perdaycoin-img {
  width: 46px;
  height: 46px;
  margin-left: 16em;
}

/* remove .logo-circle and .red-dot if unused anymore */


.news-box {
  height: 70px;
  background: rgba(17, 17, 17, 0.2);
  border: 2px solid #333;
  border-radius: 15px;
  padding: 12px;
}

.news-header {
  width: 200px;
  font-weight: bold;
  margin-bottom: 6px;
  font-size: 20px;
  margin-left: 0.5em;
  margin-top: 0.4em;
}

.news-text {
  font-size: 18px;
  margin-bottom: 10px;
  margin-top: 0.35em;
  margin-left: 0.5em;
}

.news-footer {
  margin-top: -3.65em;
}

.red-dot.small {
  width: 10px;
  height: 10px;
  margin-left: 4px;
}
